body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

article{
	padding: 15px;
	border: 1px solid #f1f1f1;
	border-radius: 7px;
	box-shadow: 10px 10px grey;
}

.vf-hero--intense{
    /*background: url('/images/5.jpeg') 0% 40% / 100% no-repeat; height: 460px;*/
    background-attachment: fixed;
    background-size: cover;
    position: relative;
  } 

  .logobar{
    padding-bottom: 10px;
    padding-top: 10px;
  }

  .input-group{
    display: flex;
  }

  .hero-image-search{
    position: relative; 
    opacity: 0.7;
    background-color: #000;
    padding: 20px 25px 0px 50px;
    bottom: 0px;
  }


  @media only screen and (min-width: 1024px) {
}

  .hero-image-search select{
    top: 2px;
    display: flex;
  }

  .hero-image-heading{
    color: #fff;
  }

  .hero-image-input{
    display: flex;
  }


  .vf-section-header__heading--is-link{
    color:#3b6fb6 !important;
  }

  .location-card{
    padding: 35px 0px 15px 40px;
  }

  .topic-card{
    padding-top: 15px;
    padding-bottom: 10px;
  }
    

svg{
    fill: #3b6fb6;
    -ms-flex-item-align: center;
    align-self: center;
    margin-left: 8px;
    transform: translateY(4px);
    transition-duration: 125ms;
    transition-timing-function: cubic-bezier(.45,.05,.55,.95);
  }

 .vf-button:visited{
    color:#fff !important;
  }

  .eventsCard{
    padding: 15px;
    border: 1px solid #e8eaed;
    margin-top: 15px;
    border-radius: 5px;
    box-shadow: 0 0 8px 0 rgba(241,243,244,.5);
    color: #666;
    /*margin-right: 10%;*/
  }

.eventsCard:hover{
  box-shadow: 0 0 8px 0 #ccc;
}

  .eventOverview{
    border-bottom:1px solid #e8eaed;
  }

  .eventLocation{
    padding:5px;
  }

  .eventDates{
    padding:5px;
  }

  .keyinfo{
    display: flex;
    flex-wrap: wrap;
  }

  .keyinfo-dates{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .registerbutton{
    display: flex;
    justify-content: flex-end;
    align-self: flex-end;
    order: 3;
    margin-left: auto;
    margin-right: 0;
  }

  .keyinfo .icon-location{
    margin-right: 10px;
  }

  .keyinfo .icon-calendar-alt{
    margin-right: 6px;
  }


  .BooksCard{
    padding: 15px;
    border: 1px solid #e8eaed;
    margin-top: 15px;
    border-radius: 5px;
    box-shadow: 0 0 8px 0 rgba(241,243,244,.5);
    color: #666;
    position: relative;
  }

  .BooksCard:hover{
    box-shadow: 0 0 8px 0 #ccc;
  }

  .book_cover_image{
    height: 150px;
    margin-bottom: 25px;
  }

  .book_cover_image_container{
    text-align: center;
  }

  .book_overview{
    border-bottom:1px solid #e8eaed;
  }

  .book_icons{
    /*position: absolute;*/
    bottom: 10px;
    margin-top: 20px;
  }

  .book_overview_container{
   /* background-color: #f1f1f1;*/
  }

  /* /////////// */

  .vf-hero--intense .vf-hero__content {
    max-width: 50%;
    grid-row: 1;
    grid-column: 2/span 1;
    box-sizing: border-box;
    position: relative;
    left: 10%;
}

.vf-hero__content{
  background-color: #fff;
  padding:20px;
    z-index: 5150;
    opacity: 0.9;
}

.vf-hero--intense .vf-hero__heading {
    font-size: 48px;
    font-weight: 600;
}

.vf-summary__title {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.333;
}

.vf-hero--intense .vf-hero__text {
    font-weight: 500;
    padding: 0;
}

.vf-hero--intense .vf-link svg {
    fill: currentColor;
    -ms-flex-item-align: center;
    align-self: center;
    /* margin-left: 8px; */
    transform: translateY(4px);
    transition-duration: 125ms;
    transition-timing-function: cubic-bezier(.45,.05,.55,.95);
}

.icon-search-custom{
  border: 1px solid #ccc;
    padding: 7px 15px 7px 15px;
    margin: 0px 0px 0px 0px;
    display: inline-table;
    background-color: #f1f1f1;
    color: #333;
}

#search_box:focus{
  border: 3px solid #007a53;
}

select:focus{
  border: 2px solid #007a53;
}

  @media (min-width: 1024px){
      .vf-masthead {
      padding-top: 435px;
    }
   
  }

  @media (max-width: 808px){
    .vf-masthead {
      padding-top: 445px;
    }

    .input-group{
      display: inline;
    }

}
